import React from 'react'

// For internal use only
export type LogoType =
  | 'dnb'
  | 'sbankenDefault'
  | 'sbankenCompact'
  | 'sbankenHorizontal'

export default function LogoSvg({
  svgParams,
  logoType,
}: {
  logoType: LogoType
  svgParams: { alt: string }
}) {
  switch (logoType) {
    case 'sbankenDefault':
      return <SbankenDefault {...svgParams} />

    case 'sbankenCompact':
      return <SbankenCompact {...svgParams} />

    case 'sbankenHorizontal':
      return <SbankenHorizontal {...svgParams} />

    case 'dnb':
      return <DnbDefault {...svgParams} />
  }
}

